import { app, h } from 'hyperapp'
import data from './data'
import icon from './icon'
import jsonp from './jsonp_new'

const replace = (text, keys) => text.replace(/{[^}]*}/g, match => keys[ match.slice(1, -1) ] || match)

const Main = ({ api, type, value }) => (
  api === 0 ? (
    (value || {}).error ?
    <p>{(value || {}).error}</p> :
    <ul>{[].concat((((value || {}).shop || {}).chirashis || {}).chirashi).map(({ title, iconUrl, pdfUrl, imgUrl, flashUrl }) => <li><a href={imgUrl || flashUrl || pdfUrl} target="_blank"><img src={iconUrl} /><span>{title}</span></a></li>)}</ul>
  ) :
  api === 1 ? <iframe src={value} /> :
  api === 2 ? <ul>{value.map(e => <li><a href={e} target="_blank">{e}</a></li>)}</ul> :
  ''
)

data.title && icon.append(data.title, { scale: 'both' })

app(
  {
    data,
    data_: {}
  },
  {
    current: value => ({ current: value }),
    area: value => ({ area: value }),
    type: value => ({ type: value }),
    data_: {
      get: ({ area, type, api, api_, name, ...rest }) => (state, actions) => {
        actions[ api_.type ] && actions[ api_.type ]({ ...rest, ...api_, api })
      },
      iframe: ({ api, i, type, url, ...rest }) => ({
        [ i ]: {
          api,
          type,
          value: replace(url, rest)
        }
      }),
      jsonp: ({ api, i, type, url, ...rest }) => (state, actions) => {
        jsonp(replace(url, rest), {
          callbackName: "JSONCallbackJscript2IdShufoo0" + Date.now()
        }).then(json => actions.set({
          [ i ]: {
            api,
            type,
            value: json
          }
        }))
      },
      urls: ({ api, i, type, urls }) => ({
        [ i ]: {
          api,
          type,
          value: [].concat(urls)
        }
      }),
      set: value => value
    }
  },
  (state, actions) => (
    document.title = [
      state.data.title,
      (state.data.shop[ state.current ] || {}).name || ''
    ].filter(e => e).join(' > '),
    <div data-current={state.current}>
      <nav>
        <ul class="area">
          <li>
            <input id="area" name="area" type="radio" onclick={() => actions.area(void 0)} checked={isNaN(state.area)} />
            <label for="area">{state.data.all}</label>
          </li>
          {state.data.area.map((e, i) => (
            <li>
              <input id={'area' + i} name="area" type="radio" onclick={() => actions.area(i)} checked={state.area === i} />
              <label for={'area' + i}>{e}</label>
            </li>
          ))}
        </ul>
        <ul class="type">
          <li>
            <input id="type" name="type" type="radio" onclick={() => actions.type(void 0)} checked={isNaN(state.type)} />
            <label for="type" data-length={
              isNaN(state.area) ?
              state.data.shop.length :
              state.data.shop.filter(e => state.area === e.area).length
            }>{state.data.all}</label>
          </li>
          {state.data.type.map((e, i) => (
            <li>
              <input id={'type' + i} name="type" type="radio" onclick={() => actions.type(i)} checked={state.type === i} />
              <label for={'type' + i} data-length={
                isNaN(state.area) ?
                state.data.shop.filter(e => e.type === i).length :
                state.data.shop.filter(e => state.area === e.area && e.type === i).length
              }>{e}</label>
            </li>
          ))}
          <li>
            <input id="type-1" name="type" type="radio" onclick={() => actions.type(-1)} checked={state.type === -1} />
            <label for="type-1" data-length={
              isNaN(state.area) ?
              state.data.shop.filter(e => e.type === -1).length :
              state.data.shop.filter(e => state.area === e.area && e.type === -1).length
            }>{state.data.other}</label>
          </li>
        </ul>
        <ul class="shop">
          {state.data.shop.map((e, i) => (
            (isNaN(state.area) || state.area === e.area) &&
            (isNaN(state.type) || state.type === e.type) &&
            <li><button type="button" onclick={() => (
              actions.current(i),
              actions.data_.get({ ...e, i, api_: state.data.api[ e.api ] })
            )} disabled={state.current === i} data-area={e.area} data-type={e.type}>{e.name}</button></li>
          ))}
        </ul>
      </nav>
      <main>
        <h1><button onclick={() => actions.current(void 0)}>&lt;</button>{(state.data.shop[ state.current ] || {}).name || ''}</h1>
        {!isNaN(state.current) && <Main {...state.data_[ state.current ] || {}} />}
      </main>
    </div>
  ),
  document.body
)
