export default {
  append: function(text, options) {
    (document.getElementsByTagName("head")[ 0 ] || document.documentElement).appendChild(this.linkElement(text, options))
  },
  linkElement: function(text, options) {
    var link = document.createElement('link')
    link.rel = 'apple-touch-icon'
    link.href = this.dataURL(text, options)
    return link
  },
  dataURL: function(text, options) {
    options = options || {}
    var size = 1 < options.size ? options.size | 0 : 256,
        canvas = document.createElement('canvas'),
        ctx = canvas.getContext('2d'),
        width,
        widest
    canvas.width = canvas.height = size
    ctx.font = options.font
    width = /(\d+)px/.exec(ctx.font)[ 1 ]
    text = (text + '').split('\n').map(function(e) { return { text: e, width: ctx.measureText(e).width } })
    widest = text.reduce(function(p, c) { return (p.width || 0) < c.width ? c : p; }, {})
    if (({ up: widest.width < size, down: size < widest.width, both: true })[ options.scale ]) {
      width = size / text.length | 0
      while (width--) {
        ctx.font = ctx.font.replace(/\d+px/, width + 'px')
        if (ctx.measureText(widest.text).width < size) {
          break
        }
      }
    }
    ctx.fillStyle = options.bgcolor || '#fff'
    ctx.fillRect(0, 0, size, size)
    ctx.fillStyle = options.color || '#000'
    ctx.textAlign = 'center'
    ctx.textBaseline = 'middle'
    text.forEach(function(e, i, a) { ctx.fillText(e.text, size / 2, (i - (a.length - 1) / 2) * width + size / 2) })
    return canvas.toDataURL()
  }
}
