var jsonp_ = function(url, options) {
  options = options || {}
  var callbackName = options.callbackName || '_' + Math.random().toString(16).slice(-12)
  var load = options.load
  var error = options.error
  var parent = document.documentElement
  var script = document.createElement('script')
  typeof load === 'function' && (
    window[ callbackName ] = load,
    script.onerror = script.onload = function(event) {
      parent.removeChild(script)
      delete window[ callbackName ]
      event.type === 'error' && typeof error === 'function' && error(url)
    },
    script.referrerPolicy = 'no-referrer',
    script.src = url.replace(/=\?/, '=' + callbackName),
    parent.appendChild(script)
  )
}

export default function(url, options) {
  return new Promise(function(resolve, reject) {
    options = options || {}
    options.load = function(json) {
      resolve(json)
    }
    options.error = function(url) {
      reject(url)
    }
    jsonp_(url, options)
  })
}
